/* HEADER */
header {
	background: linear-gradient(to top, #89a833, #809c31 40%, #b9cf6b);
}
.logo {
	float: left;
}
.phone-info {
	float: right;
	line-height: 1.2;

	span {
		color: #f2fae4;
		text-shadow: 0 1px #6b812e;
	}
}

.logo, .phone-info {
	margin: 43px 0 58px;
}

.phone-text {
	color: #fff;
	font-size: 16px;
	font-family: Calibri;
}
.phone-number {
	color: #fff;
	font-size: 20px;
	font-family: Calibri;
}

nav {
	background: linear-gradient(to top, #89a833, #809c31 40%, #b9cf6b);
	border-radius: 10px;
	position: relative;
	bottom: 28px;
}

.main {
	position: relative;
	top: -11px;
	background: #fff;
}

.main-wrapper {
	float: left;
	max-width: 654px;
}

.goods {
	margin-top: 15px;
}

.product {
	width: 150px;
	background: #fafafa;
	border: 1px solid #f4f4f4;
	border-radius: 10px;
	font-size: 13px;
	float: left;
	margin: 10px 18px 0 0;
	text-align: center;
	padding: 10px 5px;
}

.goods article:nth-of-type(4n) {
	margin-right: 0;
}

.product-image {
	background: #f4f4f4 no-repeat center;
	background-size: cover;
	border: 1px solid #f5f5f5;
	width: calc(100%);
	height: 72px;
	margin-bottom: 9px;
}
.product-title {
	color: #5f702e;
	line-height: 1.3;
	margin-bottom: 9px;
}
.product-description {
	color: #666;
	line-height: 1.3;
}
.product-price {
	color: #889564;
	font-size: 14px;
	margin: 9px 0;
}