ul.menu {
	list-style: none;
	margin: 0;
	padding: 0;

	li {
		display: inline-block;

		a {
			color: #fff;
			display: block;
			font-family: Calibri;
			font-size: 13px;
			padding: 22px 12px;
			text-align: center;
			text-decoration: none;
			text-transform: uppercase;
			width: 75px;
		}
	}

	li.active a {
		opacity: 0.8;
	}
}

.button {
	background: #fff;
	border-radius: 40px;
	color: #83a03c;
	display: inline-block;
	font-size: 14px;
}

.basket-button {
	background: #fff url(../img/basket.png) no-repeat 9px 15px;
	background-size: 18px 14px;
	border: 0;
	box-shadow: 0 1px 0 0px #61742b;
	padding: 14px 11px 9px 30px;
	position: absolute;
	right: 21px;
	top: 13px;
}
.search-button {
	font-family: Tahoma;
	color: #6f6f6f;
	border: 3px solid #89a833;
	font-size: 12px;
	padding: 5px 25px;
	width: 92px;
}

.product-buy {
	width: 120px;
}

.search-input {
	font-family: Tahoma;
	border-radius: 40px;
	border: 1px solid #e7e7e7;
	padding-left: 10px;
	width: 558px;
	height: 32px;
}

.pagination {
	background: #fafafa;
	border: 1px solid #f4f4f4;
	border-radius: 12px;
	margin-top: 16px;
	padding: 4px 14px;
}

ul.pagination {
	list-style: none;
	margin: 0;
	padding: 0;
	float: left;

	li {
		display: inline-block;

		a {
			display: block;
			margin-right: 4px;
			border-radius: 5px;
			text-decoration: none;
			background: linear-gradient(to top, #89a833, #b9cf6b);
			border: 1px solid #97b547;
			color: #fbffeb;
			text-align: center;
			padding: 0px 7px;
		}
	}	
}

.pagination-buttons {
	float: right;
	margin-top: 2px;

	a {
		text-decoration: none;
		font-size: 13px;
		font-family: Calibri;
	}

	.pagination-buttons-prev {
		color: #80934e;
		margin-right: 15px;
	}

	.pagination-buttons-next {
		color: #707070;
	}
}

aside {
	float: right;
	margin: 10px 0 0 19px;
	width: 224px;

	.aside-block {
		.aside-block-header {
			background: linear-gradient(to top, #89a833, #7b972c 20%, #bed862 80%, #b9cf6b);
			border-radius: 5px;
			color: #fff;
			padding: 15px 28px;
			text-align: center;
			text-transform: uppercase;
		}
		.aside-block-content {

			margin: 5px 0;
			
			ul.aside-content-categories {

				list-style: none;
				margin: 0;
				padding: 0;
				

				li {

					background: url(../img/marker.png) no-repeat left center;
					border-bottom: 1px solid #ededed;
					padding: 6px 0 4px;
					

					a {
						
						color: #959595;
						display: block;
						line-height: 1;
						margin-left: 19px;
						margin-right: 4px;
						text-decoration: none;
					}
				}
			}

			.aside-content-action {
				background: #fff8d8;
				border-radius: 10px;
				line-height: 1.25;
				margin-top: 5px;
				padding: 11px 9px;

				.aside-content-action-title {
					color: #8f7c38;
					margin-bottom: 21px;
				}

				.aside-content-action-content {
					color: #676767;
				}

				.aside-content-action-nextButton {
					color: #8f7c38;
					margin-left: 8px;
					text-decoration: none;
				}
			}
		}
	}
}

footer {
	background: linear-gradient(to top, #f4f4f4, #ffffff);
	box-shadow: 1px 0 0 1px #efefef;
	.footer-block {

		line-height: 1;

		&>div {
			color: #83a03c;
			margin-bottom: 8px;
		}
		margin: 10px 0 2px 0;

		.copyright {
			span {
				color: #83a03c;
			}
		}

		.certificates a {
			color: #83a03c;
		}

		float: left;
		margin-right: 26px;

		ul {
			list-style: 0;
			margin: 0;
			padding: 0;

			li {
				a {
					color: #666;
					display: block;
					font-family: Calibri;
					line-height: 1;
					margin-bottom: 8px;
					text-decoration: none;
				}
			}
		}
	}

	.footer-social {
		.footer-social-icon {
			background: linear-gradient(to top, #89a833, #b9cf6b);
			border-radius: 5px;
			
			display: inline-block;
			height: 19px;
			line-height: 1;
			margin-right: 6px;
			
			width: 19px;

			a {
				color: #fff;
				display: block;
				padding: 3px 6px;
				text-decoration: none;
			}
		}
	}

	.footer-right-block {

		float: right;
		margin-top: 27px;

		.footer-banners {

		}

		.footer-banner {
			background: #c4c4c4;
			float: left;
			height: 31px;
			margin-right: 12px;
			width: 88px;

			&:last-child {
				margin-right: 0;
			}
		}

		.designby {
			color: #c4c4c4;
			float: right;
			margin-top: 16px;
		}
	}
}
